import React from 'react'
import { animated, useSpring } from 'react-spring'
import styled from 'styled-components'

import work from '../../static/work.jpg'

const Title = styled('p')`
  position: fixed;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
`
const Card = styled(animated.div)`
  position: fixed;
  top: 50%;
  left: 50%;
  width: 192px;
  height: 192px;
  padding: 32px;
  border-radius: 16px;
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 22px 22px 30px #cccccc, -22px -22px 30px #ffffff;
`

const calc = (x, y) => [
  (y - window.innerHeight / 2) / 16,
  (x - window.innerWidth / 2) / 16,
  1.01,
]
const trans = (x, y, s) =>
  `perspective(192px) rotateX(${x}deg) rotateY(${y}deg) scale(${s}) translate(-50%, -50%)`

export default function Building() {
  const [props, set] = useSpring(() => ({
    xys: [0, 0, 1],
    config: { mass: 5, tension: 350, friction: 40 },
  }))

  return (
    <>
      <Card
        onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
        onMouseLeave={() => set({ xys: [0, 0, 1] })}
        style={{ transform: props.xys.interpolate(trans) }}
      >
        <img style={{ borderRadius: 12 }} src={work} alt="Work..." />
      </Card>
      <Title>
        Work in progress. <strong>BRB!</strong>
      </Title>
    </>
  )
}
